<template>
    <v-container>
        <v-select
            v-if="files.length"
            @change="loadMusic"
            :items="files"
            item-text="name"
            return-object
            label="Standard"
            ></v-select>
        <v-container v-if="!loaded" style="padding-top: 40px;">
    
            <v-skeleton-loader
            
                    class="mx-auto"
                    width="600"
                    height="400"
                    type="card">
            </v-skeleton-loader>
        </v-container>

        <v-container v-else style="padding-top: 40px;">
            <div>          
                <v-card
                    class="overflow-y-auto"
                    width="600"
                    height="400"
                    id="player"
                >
                    <v-list dense>
                        <v-list-item-group
                        v-model="elapsed"
                        color="primary"
                        >
                            <v-list-item
                                v-for="(second, index) in seconds"
                                :key="index"
                                @click="playFrom(second)"
                            >
                                <v-list-item-icon>
                                    <v-icon
                                    color="blue">
                                        mdi-play-circle
                                    </v-icon>
                                </v-list-item-icon>
                                
                                <v-list-item-content>
                                        <h3>{{ second }} sec ( {{ showMin(second) }} min )</h3> <h6>/ {{ max }}</h6>
                                </v-list-item-content>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>
                </v-card>
            </div>
            <div>
                <Controls :music="music" :locked="locked" @set-locked="setLocked" />
            </div>

        </v-container>
    </v-container>
</template>

<script>
import {Howl} from 'howler'
import Controls from './Controls'

export default {
    name: 'MusicVisuals',
    components: {
        Controls
    },
    props: {
        files: Array
    },
    data() {
        // https://file-examples.com/index.php/sample-audio-files/sample-mp3-download/
        return {
            autoplay: true,
            current: 1,
            duration: 0,
            elapsed: 0,
            loaded: false,
            locked: true,
            max: 100,
            min: 0,
            music: null,
            seconds: [],
            selectedSecond: 0,
            start: 0,
            stop: 0,
        }
    },
    mounted() {
        // this.loadMusic();

        // Music is a new howl, we can add event!
    },
    methods: {
        loadMusic(file) {
            let self = this;
            let interval;

            const reader = new FileReader()
            reader.readAsDataURL(file)

            reader.onload = () => {
                self.music = new Howl({
                    src: [reader.result],
                    autoplay: true,
                    html5: true,
                    // format: ['dolby', 'webm', 'mp3'],
                    onload: () => {
                        self.max = Math.ceil(self.music.duration())

                        // Add one second because it start from 0
                        self.seconds = [...Array(self.max+1).keys()]
                        
                        self.loaded = true
                        console.log('loaded', self.loaded)                       
                    },
                    onplay() {
                        console.log('play')
                        interval = setInterval(() => self.elapsed = Math.ceil(self.music.seek()), 100);
                        
                    },
                    onend() {
                        self.elapsed = 0;
                        clearInterval(interval);
                    }
                })
            }
        
                            
        },

        setLocked(val) {
            this.locked = val
        },

        playFrom(seconds) {
            this.music.seek(seconds-1)
            this.elapsed = seconds - 1

            if (!this.music.playing()) this.music.play()                    
        },

        showMin(sec) {
            return (Math.round(sec / 60 * 100) / 100).toFixed(2)
        }
    },
    watch: {
        elapsed: {
            handler(el) {
                if (this.locked) {
                    
                    // Get offsetTop
                    const ot = this.$el.querySelector('.v-list-item--active').offsetTop

                    // Set offset
                    this.$el.querySelector('#player').scrollTo({
                        top: el ? ot - 80 : 0,
                        behavior: 'smooth'
                    })
                }
                
            } 
        },

        files: {
            handler() {
                console.log(this.files)
            }
        }
    },
    destroyed() {
    },
}
</script>

