<template>
    <v-card>
        <v-col>
            <v-container class="controls">
                <v-btn
                    class="mx-2"
                    icon
                    height="75"
                    width="75"
                    :color="music.playing() ? 'gray' : 'green'"
                    @click="togglePause()"
                    >
                        <v-icon x-large>
                            mdi-{{music.playing() ? 'pause' : 'play'}}
                        </v-icon>
                </v-btn>

                <v-btn
                    class="mx-2"
                    icon
                    height="75"
                    width="75"
                    color="black"
                    @click="stopMusic()"
                    >
                        <v-icon x-large>
                            mdi-square
                        </v-icon>
                </v-btn>

                    <v-btn
                    class="mx-2"
                    icon
                    height="75"
                    width="75"
                    color="blue"
                    @click="setLocked(!locked)"
                    >
                        <v-icon x-large>
                            mdi-lock-{{ locked ? 'outline' : 'open-variant-outline' }}
                        </v-icon>
                </v-btn>
            </v-container>

            <v-spacer></v-spacer>
            <v-spacer></v-spacer>
            
            <v-container>
                <v-row>
                    <v-slider
                        v-model="volume"
                        prepend-icon="mdi-volume-high"
                        :label="volume.toString()"
                        step="1"
                    ></v-slider>
                </v-row>
            </v-container>
        </v-col>
    </v-card>
</template>

<script>
    export default {
        name: 'Controls',
        components: {},
        props: {
            music: Object,
            locked: Boolean
        },
        data() {
            return {
                volume: 100  
            }
        },
        methods: {
            togglePause() {
                this.music.playing() ?
                    this.music.pause()
                    : this.music.play();
            },

            setLocked(val) {
                this.$emit('set-locked', val)
            },

            stopMusic() {
                this.music.stop()
            },

            setVolume(v) {
                // Volume is 0.0 to 1.0
                this.music.volume(v / 100)
            }
        },
        watch: {
            volume: {
                handler(v) {
                    this.setVolume(v)
                }
            }
        }
    }
</script>

<style scoped>
    .controls {
        margin-top: 20px;
    }

    v-btn {
        margin-left: 30px;
    }
</style>