<!-- https://github.com/xdoo/vuetify_filedrop_sample/blob/master/src/components/FileDrop.vue -->

<template>   
    <v-sheet
      id="dropzone"
      ref="dzone"
      tabindex="0"
      title="Cliquez ou glissez vos fichiers ici!"
      color="indigo lighten-4"
      width="100%"
      style="cursor:pointer;"
      height="200"
      class="pa-2"
      @click="openFileupload"

    >
      <input 
        ref="upload"
        id="fileUpload"
        type="file" 
        accept="audio/*"
        multiple
        @change="uploadFiles($event, false)"
        style="display:none"/>
      <v-row justify="center">
        <v-icon
          v-if="!dragover" 
          color="indigo darken-2" 
          size="75"
        >mdi-cloud-upload-outline</v-icon>
        <v-icon
          v-if="dragover" 
          color="indigo darken-2" 
          size="75"
        >mdi-book-plus</v-icon>
      </v-row>
      <v-row justify="center">
        <span class="title indigo--text text--darken-2">Cliquez ou glissez vos fichiers ici!</span>
      </v-row>
    </v-sheet>
</template>

<script>
    export default {
      name: "FileDrop",
      // internal properties
      data() {
        return {
          formUpload: false,
          dragover: false,
          files: []
        }
      },

      methods: {
        openFileupload() {
          this.$refs.upload.click()
        },

        uploadFiles(e, isDrag) {
            this.dragover = false

            let files

            if (isDrag) files = e.dataTransfer.files
            else if (e.target.files) files = e.target.files
            else return false

            if (!files.length) return;

            for (let i=0; i<files.length; i++) {
               this.files = [...this.files, files[i]]
            }
            
            console.log(this.files)
            this.$emit('new-files', this.files)
            this.files = [];
        }
      },
    
      mounted () {
          // to register listeners, we have to know the 
          // html elements
          const dropzone = this.$el
          // register listeners on your dropzone / v-sheet
          if(dropzone) {
            // register all drag & drop event listeners
            dropzone.addEventListener("dragenter", e => {
                e.preventDefault()
                this.dragover = true
            })
            
            dropzone.addEventListener("dragleave", e => {
                e.preventDefault()
                this.dragover = false
            })

            dropzone.addEventListener("dragover", e => {
                e.preventDefault()
                this.dragover = true
            })

            dropzone.addEventListener("drop", e => {
                e.preventDefault()
                this.uploadFiles(e, true)
            })
          }
      }
    }
</script>