<template>
    <v-card>
        <v-list v-if="loaded">
            
                <v-subheader>Temp play</v-subheader>
                <v-list-item>
                    <v-slider
                        v-model="volume"
                        prepend-icon="mdi-volume-high"
                        :label="volume.toString()"
                        step="1"
                    ></v-slider>
                </v-list-item>
                <v-list-item-group>
                <v-list-item 
                    v-for="(file, index) in files" 
                    :key="index"
                    @mousedown="loadMusic(index)"
                    @mouseup="unloadMusic()"
                    @mouseleave="unloadMusic()">
                    <v-list-item-title>
                        {{ index + 1 }} -
                        {{ file.name }}
                    </v-list-item-title>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </v-card>
</template>

<script>
    import {Howl} from 'howler'

    export default {
        name: 'Sidebar',
        props: {
            files: Array           
        },
        data() {
            return {
                base64Files: [],
                loaded: false,
                volume: 100,
                music: null
            }
        },
        methods: {
            loadMusic(index) {
                let self = this;

                self.music = new Howl({
                    src: [self.base64Files[index]],
                    autoplay: true,
                    loop: true,
                    html5: true,
                    // format: ['dolby', 'webm', 'mp3'],
                    onload: () => {
                        self.max = Math.ceil(self.music.duration())

                        // Add one second because it start from 0
                        self.setVolume(self.volume)
                        self.seconds = [...Array(self.max+1).keys()]
                        console.log('loaded')                       
                    }
                })
            },

            setVolume(volume) {
                this.music.volume(volume / 100)
            },

            unloadMusic() {
                if (this.music) this.music.unload();
            },

            fileToBase64(file) {
                return new Promise((resolve) => {
                    const reader = new FileReader()
                    reader.readAsDataURL(file)
                
                    reader.onload = () => resolve(reader.result)
                })
                
            },

            async loadFiles() {
                this.loaded = false
                for (let i=0; i<this.files.length; i++) {
                    this.base64Files.push(await this.fileToBase64(this.files[i]))
                }

                this.loadMusic()
                this.loaded = true
            }
                            
        },
        watch: {
            files: {
                handler() {
                    this.loadFiles()
                }
            },

            volume: {
                handler(v) {
                    this.setVolume(v)
                }
            },
        }
    }
</script>
